import React, {Component} from "react";

class Header extends Component {
    render() {
        return(
            <div className="bg-gradient header">
                <div className="card">
                    <img src={require('../assets/images/kellyson-penha.jpg')} alt="Kellyson Penha" />
                    <h1 className="title">Kellyson Penha</h1>
                    <p className="mb-0">Front-end Developer | São Paulo - SP</p>
                </div>
            </div>
        )
    }
}

export default Header;
