import React, {Component} from "react";

class Menu extends Component {
    render() {
        return(
            <nav className="nav">
                <a href="#skills" className="nav__link">
                    <i className="lni-layers"></i>
                    <span className="nav__link__text">Skills</span>
                </a>
                <a href="#jobs" className="nav__link">
                    <i className="lni-blackboard"></i>
                    <span className="nav__link__text">Trabalhos</span>
                </a>
                <a href="#companies" className="nav__link">
                    <i className="lni-briefcase"></i>
                    <span className="nav__link__text">Experiência</span>
                </a>
                <a href="#contact" className="nav__link">
                    <i className="lni-phone"></i>
                    <span className="nav__link__text">Contato</span>
                </a>
            </nav>
        )
    }
}

export default Menu;
